import React from 'react'
import './AuthorForm.css'
import {connect} from 'react-redux'
import {withRouter} from 'react-router-dom'


function AddAuthorForm({ match,onAddAuthor }) {
  return (
    <div className="AddAuthorForm">
      <h1 className="text-center ">Add author form</h1>
      <AuthorForm onAddAuthor={onAddAuthor}/>
    </div>
  )
}

class AuthorForm extends React.Component {
  constructor(props) {
    super(props)
    this.state = {
      name: '',
      imageUrl: '',
      books:[],
      bookTemp:''
    }
    this.onFieldChange=this.onFieldChange.bind(this)
    this.handleSubmit=this.handleSubmit.bind(this)
    this.handleAddBook=this.handleAddBook.bind(this)
    this.deleteBook=this.deleteBook.bind(this)
    
  }
  onFieldChange(event){
    this.setState({
        [event.target.name]:event.target.value
    })
  }
  handleSubmit(event){
  event.preventDefault();
  this.props.onAddAuthor(this.state);
  }

  handleAddBook(){
    console.log("adddddddd")
    this.setState({
      books:this.state.books.concat([this.state.bookTemp]),
      bookTemp:''
    })
  }
  deleteBook(deletedItem){
    console.log(deletedItem,"items")
    this.setState({
      books:this.state.books.filter(item => item !== deletedItem)
    })
  }
  render() {
    return (
      <form onSubmit={this.handleSubmit} className="AddAuthorForm_input">
        <div className="form-group" >
          <div className="col-sm-5 my-1">
          <label className="sr-only" htmlFor="name">Name</label>
          <div className="input-group">
            <div className="input-group-prepend">
              <div className="input-group-text">Name</div>
            </div>
            <input type="text" name="name" className="form-control" id="inlineFormInputGroupUsername" value={this.state.name} onChange={this.onFieldChange} placeholder="Name"  required/>
          </div>
        </div>
        </div>
        <div className=" form-group">
          <div className="col-sm-5 my-1">
          <label className="sr-only" htmlFor="imageUrl">Image url</label>
          <div className="input-group">
            <div className="input-group-prepend">
              <div className="input-group-text">Image url</div>
            </div>
            <input type="text" name="imageUrl" className="form-control"  value={this.state.imageUrl} onChange={this.onFieldChange} placeholder="Image url" required/>
          </div>
        </div>
        </div>
        <div className="AddAuthorForm_input">
          <label className="bookListTitle" htmlFor="bookTemp">Books</label><br/>
          {this.state.books.map(item => 
          <p className="bookList" key={item}>{item} &nbsp;
           <button type="button" className="btn btn-sm btn-outline-primary" onClick={() => { this.deleteBook(item) }}>delete</button> 
          </p>
          )}
          
          <input type="text" name="bookTemp"   value={this.state.bookTemp} onChange={this.onFieldChange} placeholder="Books" />
          <button type="button" onClick={this.handleAddBook}>+</button>
         
        </div>
       
        <input type="submit" className="btn btn-primary addBook" value="ADD" />
      </form>
    )
  }
}
function mapDispatchToProps(dispatch,props){
  return {
      onAddAuthor : (author) => {
        dispatch({type:'ADD_AUTHOR',author});
        props.history.push('/')
      }
  }
}
export default  withRouter(connect(() => {},mapDispatchToProps) (AddAuthorForm))