import React from 'react';

import './App.css';
import './bootstrap-social.css'

import { Link } from 'react-router-dom'
import { connect } from 'react-redux'
import {Modal,Button} from 'react-bootstrap'


//reat-redux 

function mapStateToProps(state) {
  console.log(state,"mapStateToProps")
  return {
    turnData: state.turnData,
    highlight:state.highlight,
    answer:state.answer,
    noOfCorrectAns:state.noOfCorrectAns,
    noOfWrongAns:state.noOfWrongAns
  }
}

function mapDispatchToProps(dispatch) {
  return {
    onAnswerSelected : (answer) => {
      dispatch({ type: "ANSWER_SELECTED" ,answer})
    },
    onContinue: () => {
      dispatch({ type: "CONTINUE" })
    }
  }
}

//const connect = ReactRedux.connect;

// define the Container component here:
const AuthorQuiz = connect(mapStateToProps, mapDispatchToProps)(AuthorQuizComp)



function AuthorQuizComp({ turnData,highlight,answer, onAnswerSelected, onContinue ,noOfCorrectAns,noOfWrongAns }) {
  
  return (

    <div className="container-fluid">
      <Hero />
      <Turn {...turnData} highlight={highlight}  answer={answer} onAnswerSelected={onAnswerSelected} onContinue={onContinue} />
       
      <p><Link to="/add">Add an author</Link></p>  
     
      <div className="float-right">End quiz</div>
      <EndQuiz noOfCorrectAns={noOfCorrectAns} noOfWrongAns={noOfWrongAns}/>

      <Footer {...turnData}/>
    </div>

  );
}
function Hero() {
  return (
    <div className="jumbotron jumbotron-fluid text-center">
      <h1>Author Quiz</h1>
      <p>Select the book written by the author</p>
    </div>
  )
}



function Turn({author,books,onAnswerSelected,onContinue,highlight,answer}) {
    return (
      <React.Fragment>
        <div className={highlight} >
          <div className="col-md-4">
            <img src={author.imageUrl} alt="" className="author-image" />
            <div className="author-name">
            <span>{author.name}</span>
          </div>
          </div>
          
          <div className="col-md-6">
            {books.map(item => {
              //console.log(item)
              return <Book title={item} key={item} onClicks={onAnswerSelected} />
            })}
          </div>
        </div>
        <div className="message">
           {answer === undefined ? null :answer ?
            <div className="alert alert-success" role="alert">
              Answer is correct !!!
      </div> :
            <div className="alert alert-danger" role="alert">
              Answer is wrong !!! Try again
   </div>

          } 
          <div >
            <Continue show={answer} onContinue={onContinue} />
          </div>
        </div>
      </React.Fragment>
    )
  
}



function Book({ title, onClicks }) {
  return (
    <div className="answer" onClick={() => { onClicks(title) }}>
      <h4>{title}</h4>

    </div>
  )
}


function Continue({ show, onContinue }) {
  return (
    <div>
      {show ?
        <button className="btn btn-primary float-right" onClick={() => { onContinue() }}>Continue</button>
        : null
      }
    </div>
  )
}


