class EndQuiz extends React.Component {
  constructor(props) {
    super(props);
  
    this.state = {
      open: false
    };
    this.handleClick=this.handleClick.bind(this)
    this.handleClose=this.handleClose.bind(this)
  }
  handleClick(){
    this.setState({
      open: true
    })
  }
  handleClose(){
    window.location.reload()
  }
  render(){
    let closeModal = () => /* this.setState({ open: false }) */{
      window.location.reload()
    }
 
    console.log(this.state.open)
 
    return (
      <div className="float-right">
        <button type="button" className="btn btn-danger" onClick={this.handleClick}>End Quiz</button>
 
        <Modal
          show={this.state.open}
          onHide={closeModal}
          aria-labelledby="ModalHeader"
        >
          <Modal.Header closeButton>
            <Modal.Title id='ModalHeader'>Quiz Results</Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <p>You have answered <strong>{this.props.noOfCorrectAns}</strong> questions correctly out of {this.props.noOfCorrectAns+this.props.noOfWrongAns} </p>
          </Modal.Body>
          <Modal.Footer>
           
          <Button variant="secondary" onClick={this.handleClose}>
            Close
          </Button>
 
 
            
            
          </Modal.Footer>
        </Modal>
      </div>
    )
  }
}

function Footer({author}) {
  return (
    <div >

      <p className="text-muted credit">Image Source : {author.imageSource}</p>
      <footer className="page-footer font-small special-color-dark pt-4">

        <div className="container">

          <ul className="social-icons">
            <a className="btn btn-social-icon btn-twitter" href="https://twitter.com/Vasantha_KumarJ"  rel="norefnoopener noreferrererrer"  target="_blank">
              <i className="fa fa-twitter "></i>
            </a>
            <a className="btn btn-social-icon btn-github" href="https://github.com/jvasanthakumar94/"   rel="noopener noreferrer"  target="_blank">
              <span className="fa fa-github"></span>
            </a>
            <a className="btn btn-social-icon btn-linkedin" href="https://www.linkedin.com/in/jvasantha-kumar/"  rel="noopener noreferrer"  target="_blank">
              <span className="fa fa-linkedin"></span>
            </a>
          </ul> 

        </div>

        <div className="footer-copyright text-center py-3">

          <span>Made with <i className="fa fa-heart"></i> by Vasanth</span>
        </div>

      </footer>

    </div>
  )
}

export default AuthorQuiz;



