let initailState = {
  authors,
  turnData: getTurnData(authors),
  highlight: 'row turn',
  answer: undefined,
  noOfCorrectAns: 0,
  noOfWrongAns: 0,
  ansArray: []

}


//redux

function reducer(state = initailState, action) {

  console.log(state)
  console.log(state.turnData, "turnData11")
  switch (action.type) {

    case 'ANSWER_SELECTED':
      const isCorrectAnswer =
        state.turnData.author.books.some(item => {
          // console.log(item, "item")
          // console.log(action.answer, "answer")
          return item === action.answer
        })

      console.log(isCorrectAnswer, "isCorrectAnswer")

      let ansArray = state.ansArray;
      console.log(ansArray.indexOf(state.turnData.author.name), "index")
      let tempCorrect = state.noOfCorrectAns, tempWrong = state.noOfWrongAns;
      if (ansArray.indexOf(state.turnData.author.name) < 0) {
        ansArray.push(state.turnData.author.name)

        console.log(ansArray, "ansArray")

        if (isCorrectAnswer) {
          //console.log(state.noOfCorrectAns)

          tempCorrect = (state.noOfCorrectAns) + 1
          // console.log(tempCorrect)

        } else {

          tempWrong = (state.noOfWrongAns) + 1
        }
      }

      return Object.assign({}, state, {

        highlight: (isCorrectAnswer ? 'row turn bg-success' : 'row turn bg-danger'),
        answer: isCorrectAnswer,
        noOfCorrectAns: tempCorrect,
        noOfWrongAns: tempWrong
      })
    case 'CONTINUE':
      return Object.assign({}, state, {
        turnData: getTurnData(state.authors),
        highlight: 'row turn',
        answer: undefined
      })
    case 'ADD_AUTHOR':
      return Object.assign({}, state, {
        authors: state.authors.concat(action.author)
      })
    default:
      return state;
  }

}

let store = Redux.createStore(reducer)
