function getTurnData(authors) {
  console.log(authors)

  //p is the empty array , c -> each obj , p is concat with each books
  //which results in club all books in a obj
  const allBooks = authors.reduce((p, c, i) => p.concat(c.books), [])
  //shuffle & sample is a module in npm 'underscore' package
  const fourRandomBooks = shuffle(allBooks).slice(0, 4)
  console.log(allBooks, "allBooks")
  console.log(fourRandomBooks, "fourRandomBooks")
  const answer = sample(fourRandomBooks)
  console.log(answer, "answers")
  return {
    books: fourRandomBooks,
    author: authors.find(author =>
      author.books.some(item => {
        //console.log(item, "item")
        // console.log(answer, "answer")
        return item === answer
      })
    )
  }
}


